<template>
  <div class="biorhythm-container">
    <div class="page-header">
      <h1>生物节律查询</h1>
      <p class="subtitle">了解您的身体、情感和智力周期变化</p>
      <div class="divider"></div>
    </div>

    <div class="query-section">
      <div class="query-card">
        <h3>输入您的出生日期</h3>
        <div class="input-group">
          <label for="birthdate">出生日期：</label>
          <input 
            type="date" 
            id="birthdate" 
            v-model="birthdate"
            class="date-input"
          >
        </div>
        <div class="input-group">
          <label for="targetDate">目标日期：</label>
          <input 
            type="date" 
            id="targetDate" 
            v-model="targetDate"
            class="date-input"
          >
        </div>
        <button class="calculate-btn" @click="calculateBiorhythm">计算生物节律</button>
      </div>
    </div>

    <div class="result-section" v-if="showResults">
      <div class="result-card">
        <h3>您的生物节律分析结果</h3>
        <div class="result-date">
          <p>分析日期: <strong class="highlight-data">{{ formatDate(targetDate) }}</strong></p>
          <p>年龄: <strong class="highlight-data">{{ age }}</strong> 岁</p>
          <p>出生后天数: <strong class="highlight-data">{{ daysSinceBirth }}</strong> 天</p>
        </div>
        
        <div class="biorhythm-chart">
          <canvas ref="biorhythmChart" width="800" height="400"></canvas>
        </div>
        
        <div class="biorhythm-values">
          <div class="value-item physical">
            <div class="value-icon">
              <i class="fas fa-running"></i>
            </div>
            <div class="value-content">
              <h4>身体节律: <span class="value-number">{{ biorhythm.physical.toFixed(2) }}</span></h4>
              <div class="progress-bar">
                <div class="progress" :style="{ width: getProgressWidth(biorhythm.physical), backgroundColor: getProgressColor(biorhythm.physical) }"></div>
              </div>
              <p>{{ getPhysicalDescription() }}</p>
            </div>
          </div>
          
          <div class="value-item emotional">
            <div class="value-icon">
              <i class="fas fa-heart"></i>
            </div>
            <div class="value-content">
              <h4>情感节律: <span class="value-number">{{ biorhythm.emotional.toFixed(2) }}</span></h4>
              <div class="progress-bar">
                <div class="progress" :style="{ width: getProgressWidth(biorhythm.emotional), backgroundColor: getProgressColor(biorhythm.emotional) }"></div>
              </div>
              <p>{{ getEmotionalDescription() }}</p>
            </div>
          </div>
          
          <div class="value-item intellectual">
            <div class="value-icon">
              <i class="fas fa-brain"></i>
            </div>
            <div class="value-content">
              <h4>智力节律: <span class="value-number">{{ biorhythm.intellectual.toFixed(2) }}</span></h4>
              <div class="progress-bar">
                <div class="progress" :style="{ width: getProgressWidth(biorhythm.intellectual), backgroundColor: getProgressColor(biorhythm.intellectual) }"></div>
              </div>
              <p>{{ getIntellectualDescription() }}</p>
            </div>
          </div>
        </div>
        
        <div class="biorhythm-explanation">
          <h3>生物节律说明</h3>
          <p>生物节律理论源于19世纪末至20世纪初，由多位研究者共同发展而来。该理论认为，人体存在三种基本的生理周期：</p>
          <ul>
            <li><strong>身体节律 (23天周期)：</strong> 影响体力、耐力、抵抗力、协调性、健康状况和身体恢复能力</li>
            <li><strong>情感节律 (28天周期)：</strong> 影响情绪、感受性、创造力、情感稳定性、领悟力和人际关系</li>
            <li><strong>智力节律 (33天周期)：</strong> 影响思维能力、逻辑分析、学习效率、记忆力、警觉性和沟通能力</li>
          </ul>
          
          <h4>理论起源</h4>
          <p>该理论的主要贡献者包括：</p>
          <ul>
            <li>维也纳大学心理学教授Hermann Swoboda，通过研究发烧病人的周期性病情变化，提出了23天身体周期和28天情感周期</li>
            <li>柏林鼻喉专家Wilhelm Fliess，独立研究得出相似结论</li>
            <li>因斯布鲁克大学工程学教授Alfred Teltscher，通过观察学生的学习表现，发现了33天的智力周期</li>
          </ul>
          
          <h4>计算方法</h4>
          <p>生物节律值通过正弦函数计算，公式如下：</p>
          <div class="formula-box">
            <p>身体节律 = sin(2π × t ÷ 23)</p>
            <p>情感节律 = sin(2π × t ÷ 28)</p>
            <p>智力节律 = sin(2π × t ÷ 33)</p>
            <p>其中，t 表示从出生日期到目标日期的天数</p>
          </div>
          
          <h4>节律值解读</h4>
          <ul>
            <li><strong>正值 (0到1)：</strong> 表示该方面处于上升或高效期，能力增强</li>
            <li><strong>零值 (接近0)：</strong> 表示临界日，该方面可能不稳定或处于转变期，建议避免重要决策</li>
            <li><strong>负值 (-1到0)：</strong> 表示该方面处于下降或低效期，能力相对减弱</li>
          </ul>
          
          <h4>科学观点</h4>
          <p>需要注意的是，生物节律理论在科学界存在争议。支持者认为它是一门跨学科的新科学，而批评者则质疑其科学依据，主要争议点包括：</p>
          <ul>
            <li>周期天数(23、28、33天)的确定缺乏充分科学依据</li>
            <li>以出生时刻作为起点的合理性存疑</li>
            <li>实验验证结果不一致</li>
          </ul>
          
          <p>尽管存在争议，生物节律分析仍可作为个人生活规划的参考工具。您可以根据分析结果，合理安排工作、学习和生活，在高效期完成重要任务，在低效期注意休息和调整。</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js';


export default {
  name: 'BiorhythmQuery',
  data() {
    return {
      birthdate: '',
      targetDate: new Date().toISOString().split('T')[0],
      showResults: false,
      biorhythm: {
        physical: 0,
        emotional: 0,
        intellectual: 0
      },
      daysSinceBirth: 0,
      age: 0,
      chart: null
    };
  },
  methods: {
    calculateBiorhythm() {
      if (!this.birthdate) {
        alert('请输入出生日期');
        return;
      }

      const birthDate = new Date(this.birthdate);
      const targetDate = new Date(this.targetDate);
      
      // 计算出生至今的天数
      const timeDiff = targetDate.getTime() - birthDate.getTime();
      this.daysSinceBirth = Math.floor(timeDiff / (1000 * 3600 * 24));
      
      // 计算年龄
      this.age = this.calculateAge(birthDate, targetDate);
      
      // 步骤2：计算各周期余数
      const physicalRemainder = this.daysSinceBirth % 23;
      const emotionalRemainder = this.daysSinceBirth % 28;
      const intellectualRemainder = this.daysSinceBirth % 33;
      
      // 步骤3：计算每日状态值
      this.biorhythm.physical = Math.sin(2 * Math.PI * physicalRemainder / 23);
      this.biorhythm.emotional = Math.sin(2 * Math.PI * emotionalRemainder / 28);
      this.biorhythm.intellectual = Math.sin(2 * Math.PI * intellectualRemainder / 33);
      
      this.showResults = true;
      
      // 在下一个DOM更新周期后绘制图表
      this.$nextTick(() => {
        this.drawChart();
      });
    },
    
    calculateAge(birthDate, currentDate) {
      let age = currentDate.getFullYear() - birthDate.getFullYear();
      const m = currentDate.getMonth() - birthDate.getMonth();
      
      if (m < 0 || (m === 0 && currentDate.getDate() < birthDate.getDate())) {
        age--;
      }
      
      return age;
    },
    
    formatDate(dateString) {
      const date = new Date(dateString);
      return `${date.getFullYear()}年${date.getMonth() + 1}月${date.getDate()}日`;
    },
    
    getProgressWidth(value) {
      // 将-1到1的值转换为0到100%的宽度
      return `${((value + 1) / 2 * 100).toFixed(0)}%`;
    },
    
    getProgressColor(value) {
      // 根据值的不同返回不同的颜色
      if (value > 0.5) return '#4CAF50'; // 高值 - 绿色
      if (value > 0) return '#8BC34A'; // 中高值 - 浅绿色
      if (value > -0.5) return '#FFC107'; // 中低值 - 黄色
      return '#F44336'; // 低值 - 红色
    },
    
    getPhysicalDescription() {
      const value = this.biorhythm.physical;
      
      // 判断是否为临界日
      if (this.isCriticalDay(value)) {
        return '今天是身体节律的临界日，状态不稳定，建议避免重要的体力活动和决策。';
      }
      
      if (value > 0.7) return '您的身体状态极佳，适合进行体力活动和运动训练。';
      if (value > 0.3) return '您的身体状态良好，精力充沛，适合完成需要体力的工作。';
      if (value > 0) return '您的身体状态处于高潮期，体能较为充沛。';
      if (value > -0.3) return '您的身体状态处于低潮期，注意适当休息，不要过度劳累。';
      if (value > -0.7) return '您的身体状态较差，建议减少体力消耗，多休息。';
      return '您的身体状态处于低谷期，应避免剧烈运动，注意身体保养。';
    },
    
    getEmotionalDescription() {
      const value = this.biorhythm.emotional;
      
      // 判断是否为临界日
      if (this.isCriticalDay(value)) {
        return '今天是情感节律的临界日，情绪可能不稳定，建议避免重要的情感决策。';
      }
      
      if (value > 0.7) return '您的情绪状态极佳，充满积极性和创造力，适合社交活动。';
      if (value > 0.3) return '您的情绪状态良好，心情愉悦，适合处理人际关系。';
      if (value > 0) return '您的情绪状态处于高潮期，情绪较为积极。';
      if (value > -0.3) return '您的情绪状态处于低潮期，可能有些波动，注意情绪管理。';
      if (value > -0.7) return '您的情绪状态较差，可能感到焦虑或不安，建议放松心情。';
      return '您的情绪状态处于低谷期，应避免重要的情感决策，多进行自我关怀。';
    },
    
    getIntellectualDescription() {
      const value = this.biorhythm.intellectual;
      
      // 判断是否为临界日
      if (this.isCriticalDay(value)) {
        return '今天是智力节律的临界日，思维可能不稳定，建议避免重要的决策和复杂思考。';
      }
      
      if (value > 0.7) return '您的智力状态极佳，思维敏捷，适合学习新知识和解决复杂问题。';
      if (value > 0.3) return '您的智力状态良好，逻辑思维清晰，适合进行分析和决策。';
      if (value > 0) return '您的智力状态处于高潮期，思维较为敏捷。';
      if (value > -0.3) return '您的智力状态处于低潮期，注意集中注意力，避免复杂的思维活动。';
      if (value > -0.7) return '您的智力状态较差，可能感到思维迟缓，建议简化工作内容。';
      return '您的智力状态处于低谷期，应避免重要的决策和复杂的思维任务。';
    },
    
    isCriticalDay(value) { 
      return Math.abs(value) < 0.1; 
    },
    
    drawChart() {
      const ctx = this.$refs.biorhythmChart.getContext('2d');
      
      // 如果已有图表，先销毁
      if (this.chart) {
        this.chart.destroy();
      }
      
      // 生成前后15天的数据
      const days = 31; // 总共显示31天
      const midPoint = Math.floor(days / 2); // 中间点
      
      const labels = [];
      const physicalData = [];
      const emotionalData = [];
      const intellectualData = [];
      
      const targetDateObj = new Date(this.targetDate);
      
      for (let i = 0; i < days; i++) {
        const currentDate = new Date(targetDateObj);
        currentDate.setDate(targetDateObj.getDate() - midPoint + i);
        
        // 添加日期标签
        labels.push(currentDate.getDate() + '日');
        
        // 计算当天的生物节律
        const dayDiff = Math.floor((currentDate - new Date(this.birthdate)) / (1000 * 3600 * 24));
        
        // 计算各周期余数
        const physicalRemainder = dayDiff % 23;
        const emotionalRemainder = dayDiff % 28;
        const intellectualRemainder = dayDiff % 33;
        
        // 计算状态值
        physicalData.push(Math.sin(2 * Math.PI * physicalRemainder / 23));
        emotionalData.push(Math.sin(2 * Math.PI * emotionalRemainder / 28));
        intellectualData.push(Math.sin(2 * Math.PI * intellectualRemainder / 33));
      }
      
      // 创建图表
      this.chart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: labels,
          datasets: [
            {
              label: '身体节律',
              data: physicalData,
              borderColor: '#F44336',
              backgroundColor: 'rgba(244, 67, 54, 0.1)',
              tension: 0.4,
              borderWidth: 2,
              pointRadius: 3,
              pointBackgroundColor: '#F44336'
            },
            {
              label: '情感节律',
              data: emotionalData,
              borderColor: '#4CAF50',
              backgroundColor: 'rgba(76, 175, 80, 0.1)',
              tension: 0.4,
              borderWidth: 2,
              pointRadius: 3,
              pointBackgroundColor: '#4CAF50'
            },
            {
              label: '智力节律',
              data: intellectualData,
              borderColor: '#2196F3',
              backgroundColor: 'rgba(33, 150, 243, 0.1)',
              tension: 0.4,
              borderWidth: 2,
              pointRadius: 3,
              pointBackgroundColor: '#2196F3'
            }
          ]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          scales: {
            y: {
              min: -1,
              max: 1,
              ticks: {
                callback: function(value) {
                  if (value === 1) return '高峰';
                  if (value === 0) return '临界';
                  if (value === -1) return '低谷';
                  return '';
                }
              },
              grid: {
                color: function(context) {
                  if (context.tick.value === 0) {
                    return 'rgba(0, 0, 0, 0.5)';
                  }
                  return 'rgba(0, 0, 0, 0.1)';
                },
                lineWidth: function(context) {
                  if (context.tick.value === 0) {
                    return 2;
                  }
                  return 1;
                }
              }
            },
            x: {
              grid: {
                display: false
              }
            }
          },
          plugins: {
            tooltip: {
              callbacks: {
                title: function(tooltipItems) {
                  const index = tooltipItems[0].dataIndex;
                  const currentDate = new Date(targetDateObj);
                  currentDate.setDate(targetDateObj.getDate() - midPoint + index);
                  return currentDate.getFullYear() + '年' + 
                         (currentDate.getMonth() + 1) + '月' + 
                         currentDate.getDate() + '日';
                },
                label: function(context) {
                  let label = context.dataset.label || '';
                  if (label) {
                    label += ': ';
                  }
                  if (context.parsed.y !== null) {
                    label += context.parsed.y.toFixed(2);
                  }
                  return label;
                }
              }
            },
            annotation: {
              annotations: {
                line1: {
                  type: 'line',
                  xMin: midPoint,
                  xMax: midPoint,
                  borderColor: 'rgba(0, 0, 0, 0.5)',
                  borderWidth: 2,
                  borderDash: [5, 5],
                  label: {
                    content: '今天',
                    enabled: true,
                    position: 'top'
                  }
                }
              }
            }
          }
        }
      });
    }
  }
};
</script>

<style scoped>
