}

.value-content p {
  margin-top: 15px;
  line-height: 1.6;
  color: #495057;
  font-size: 16px;
  font-weight: 500;
}

.biorhythm-explanation {
  margin-top: 45px;
  padding-top: 35px;
  border-top: 2px solid #e9ecef;
}

.biorhythm-explanation h3 {
  margin-bottom: 25px;
  color: #2c3e50;
  font-size: 24px;
  font-weight: 700;
  position: relative;
  padding-left: 15px;
}

