<template>
  <div class="big-platform">
    <!-- 侧边栏 -->
    <div class="sidebar">
      <div class="logo-container">
        <img src="@/assets/img/zhimingzhelogo.png" alt="知命者" class="logo">
        <h3>知命者大平台</h3>
      </div>
      <ul class="nav-menu">
        <li class="nav-item" :class="{ active: activePage === 'home' }" @click="navigateTo('home')">
          <i class="fas fa-home"></i>
          <span>平台首页</span>
        </li>
        <li class="nav-item" :class="{ active: activePage === 'biorhythm' }" @click="navigateTo('biorhythm')">
          <i class="fas fa-heartbeat"></i>
          <span>生物节律查询</span>
        </li>
        <!-- 未来可添加更多导航项 -->
        <li class="nav-item disabled">
          <i class="fas fa-user-circle"></i>
          <span>个人命运分析</span>
        </li>
        <li class="nav-item disabled">
          <i class="fas fa-calendar-alt"></i>
          <span>运势日历</span>
        </li>
        <li class="nav-item disabled">
          <i class="fas fa-chart-line"></i>
          <span>命运趋势图</span>
        </li>
      </ul>
    </div>

    <!-- 主内容区 -->
    <div class="main-content">
      <!-- 根据当前活动页面显示不同内容 -->
      <div v-if="activePage === 'home'" class="home-content">
        <div class="welcome-section">
          <h1>欢迎来到知命者大平台</h1>
          <p class="subtitle">传统智慧与现代科技的融合平台</p>
          <div class="divider"></div>
        </div>

        <div class="platform-intro">
          <div class="intro-card">
            <div class="card-icon">
              <i class="fas fa-star"></i>
            </div>
            <h3>平台简介</h3>
            <p>知命者大平台是一个融合传统文化智慧与现代科学技术的综合性平台，致力于为用户提供科学、系统的命运分析与改善方案。通过传统与现代方法的结合，我们能够更全面、更深入地获取生命信息，为用户提供精准的命运解读与优化建议。</p>
          </div>
        </div>

        <div class="feature-grid">
          <div class="feature-item">
            <div class="feature-icon">
              <i class="fas fa-heartbeat"></i>
            </div>
            <h3>生物节律查询</h3>
            <p>基于科学理论的生物节律分析，帮助您了解身体、情感和智力的周期变化，优化日常安排。</p>
            <button class="btn-feature" @click="navigateTo('biorhythm')">立即体验</button>
          </div>
          
          <div class="feature-item">
            <div class="feature-icon">
              <i class="fas fa-user-circle"></i>
            </div>
            <h3>个人命运分析</h3>
            <p>结合传统命理与现代心理学，为您提供全面的个人命运分析报告。</p>
            <button class="btn-feature disabled">即将上线</button>
          </div>
          
          <div class="feature-item">
            <div class="feature-icon">
              <i class="fas fa-calendar-alt"></i>
            </div>
            <h3>运势日历</h3>
            <p>每日运势预测，助您把握吉日良机，避开凶险时刻。</p>
            <button class="btn-feature disabled">即将上线</button>
          </div>
          
          <div class="feature-item">
            <div class="feature-icon">
              <i class="fas fa-chart-line"></i>
            </div>
            <h3>命运趋势图</h3>
            <p>可视化展示您的命运走势，提前预知人生高低起伏。</p>
            <button class="btn-feature disabled">即将上线</button>
          </div>
        </div>
      </div>

      <!-- 生物节律查询页面 -->
      <biorhythm-query v-if="activePage === 'biorhythm'"></biorhythm-query>
    </div>
  </div>
</template>

<script>
import BiorhythmQuery from './BiorhythmQuery.vue';

export default {
  name: 'ZhimingzheBigPlatform',
  components: {
    BiorhythmQuery
  },
  data() {
    return {
      activePage: 'home'
    };
  },
  methods: {
    navigateTo(page) {
      if (page === 'home' || page === 'biorhythm') {
        this.activePage = page;
      }
    }
  }
};
</script>

<style scoped>
.big-platform {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 9999;  /* 增加z-index值 */
  background-color: #fff;
  overflow: auto;
}

/* 侧边栏样式 */
.sidebar {
  width: 250px;
  background: linear-gradient(to bottom, #8e44ad, #6c3483);
  color: #fff;
  padding: 20px 0;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
  position: fixed;
  height: 100vh;
  overflow-y: auto;
}

.logo-container {
  text-align: center;
  padding: 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 20px;
}

.logo {
  width: 70px;  /* 调整宽度 */
  height: 70px;  /* 调整高度 */
  border-radius: 4px;
  margin-bottom: 10px;
  background-color: #fff;
  padding: 8px;  /* 增加内边距 */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  object-fit: contain;  /* 确保图片适应容器 */
}

.nav-menu {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-item {
  padding: 15px 20px;
  cursor: pointer;
  transition: all 0.3s;
  display: flex;
  align-items: center;
}

.nav-item:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.nav-item.active {
  background-color: rgba(255, 255, 255, 0.2);
  border-left: 4px solid #fff;
}

.nav-item.disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.nav-item i {
  margin-right: 10px;
  font-size: 18px;
}

/* 主内容区样式 */
.main-content {
  flex: 1;
  padding: 30px;
  margin-left: 250px;
}

.welcome-section {
  text-align: center;
  margin-bottom: 40px;
}

.welcome-section h1 {
  color: #333;
  font-size: 32px;
  margin-bottom: 10px;
}

.subtitle {
  color: #666;
  font-size: 18px;
}

.divider {
  height: 3px;
  width: 100px;
  background: linear-gradient(to right, #8e44ad, #3498db);
  margin: 20px auto;
  border-radius: 3px;
}

.platform-intro {
  margin-bottom: 40px;
}

.intro-card {
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  text-align: center;
}

.card-icon {
  width: 70px;
  height: 70px;
  background: linear-gradient(to right, #8e44ad, #3498db);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
}

.card-icon i {
  font-size: 30px;
  color: #fff;
}

.intro-card h3 {
  color: #333;
  margin-bottom: 15px;
}

.intro-card p {
  color: #666;
  line-height: 1.6;
}

.feature-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 25px;
}

.feature-item {
  background-color: #fff;
  border-radius: 10px;
  padding: 25px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  text-align: center;
  transition: transform 0.3s;
}

.feature-item:hover {
  transform: translateY(-5px);
}

.feature-icon {
  width: 60px;
  height: 60px;
  background: linear-gradient(to right, #8e44ad, #3498db);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 15px;
}

.feature-icon i {
  font-size: 24px;
  color: #fff;
}

.feature-item h3 {
  color: #333;
  margin-bottom: 10px;
}

.feature-item p {
  color: #666;
  margin-bottom: 20px;
  line-height: 1.5;
}

.btn-feature {
  background: linear-gradient(to right, #8e44ad, #3498db);
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-feature:hover {
  opacity: 0.9;
  transform: scale(1.05);
}

.btn-feature.disabled {
  background: #ccc;
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .sidebar {
    width: 70px;
    overflow: hidden;
  }
  
  .sidebar .logo-container h3,
  .sidebar .nav-item span {
    display: none;
  }
  
  .main-content {
    margin-left: 70px;
  }
  
  .feature-grid {
    grid-template-columns: 1fr;
  }
}
</style>


